html, body, #root {
  padding: 0;
  margin: 0;
}

html {
  background-color: #f5f2f0;  
}
.App {
  width: 100%;
  height: 100%;
  font-size: 18px;
  font-family: 'Hiragino Kaku Gothic ProN W3', sans-serif;
}



/* %%%%%%%%%%%%%%%%%%%%% Others %%%%%%%%%%%%%%%%%%%%% */

.fadeIn {
  animation-name: fadeIn;
  animation-duration: 1.5s;
  animation-timing-function: ease;
  animation-delay: 0.2s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  opacity: 0;
}
@keyframes fadeIn {
  0%{
    opacity: 0;
  }
  100%{
    opacity: 1;
  }
}

.page-title {
  font-size: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 0;

}

.page-title:before, .page-title:after {
  border-top: 1px solid;
  content: "";
  width: 30%;
  color: rgba(0,0,0,0.5);
}

.page-title:before {
  margin-right: 1em;
}

.page-title::after {
  margin-left: 1em;
}

.container {
  padding-top: 10px;
}

.section-title{
  margin-left: 8%;
  font-size: 30px;
  width: 80%;
}

.section {
  padding-top: 30px;
}

.fa {
  vertical-align: -5px;
  margin-right: 7px;
}

a {
  color: black;
  opacity: 0.7;
  text-decoration: none;
  transition: all 0.25s;
}

a:hover {
  color: #58ac58;
}

.content {
  display: block;
  margin-top: 15px;
}

.content-text {
  align-items: center;
}

/* %%%%%%%%%%%%%%%%%%%%% header %%%%%%%%%%%%%%%%%%%%% */

header {
  width: 100%;
  height: 60px;
  padding-bottom: 20px;
}

nav {
  padding-top: 20px;
  padding-right: 40px;
}

nav ul {
  float: right;
}

nav ul li {
  list-style: none;
  float: left;
  padding-right: 40px;
}

.logo {
  padding: 40px;
}

.logo-img {
  width: 60px;
}

/* %%%%%%%%%%%%%%%%%%%%% top %%%%%%%%%%%%%%%%%%%%% */
.top {
  text-align: center;
  padding-top: 100px;

}

.name {
  font-size: 60px;
  font-family: cursive;
}

.top-message {
  opacity: 0.7;
}

/* %%%%%%%%%%%%%%%%%%%%% about %%%%%%%%%%%%%%%%%%%%% */

.about .contents {
  width: 80%;
}

.profile-container {
  margin-top: 50px;
  margin-left: 15%;
  align-items: center;
  display: flex;
}

.about .contents {
  margin-left: 100px;
}

.face-image {
  width: 150px;
  border-radius: 50%;
}



.about .content-title {
  width: 100px;
}

.about a {
  opacity: 1;
}

.career-container {
  margin-left: 8%;
}

.career .content-title {
  width: 160px;
}

.career .content {
  margin-top: 30px;
}

.career .fa {
  margin-right: 15px;
}

.fa-before {
  vertical-align: -5px;
  margin-right: 7px;
}